.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal-content {
  background: #fff;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  z-index: 10000;
}

.modal-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 20px 10px;
}

.modal-title {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
  color: #333;
}

.logo-section {
  text-align: center;
  padding: 40px 20px 30px;
}

.logo {
  max-width: 100%;
  height: auto;
  width: auto;
  margin-bottom: 20px;
  object-fit: contain;
}

.app-title {
  font-size: 28px;
  font-weight: bold;
  color: #333;
  margin: 0 0 8px 0;
  letter-spacing: -0.5px;
}

.app-subtitle {
  font-size: 16px;
  color: #666;
  margin: 0;
}

.start-section {
  padding: 0 20px 30px;
}

.start-btn {
  width: 100%;
  background: #ddd;
  color: #666;
  border: none;
  padding: 16px;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  
  &:hover {
    background: #ccc;
  }
}

.social-login-section {
  padding: 0 20px 30px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 15px;
  justify-items: center;
  align-items: center;
  max-width: 200px;
  margin: 0 auto;
}

.social-btn {
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: transform 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  
  &:hover {
    transform: scale(1.05);
  }
  
  &.kakao {
    background: #FEE500;
  }
  
  &.naver {
    background: #03C75A;
  }
  
  &.instagram {
    background: linear-gradient(45deg, #f09433 0%, #e6683c 25%, #dc2743 50%, #cc2366 75%, #bc1888 100%);
  }
  
  &.facebook {
    background: #1877F2;
  }
  
  &.google {
    background: #fff;
    border: 1px solid #ddd;
  }
  
  &.twitter {
    background: #000;
  }
}

.social-icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.naver-n {
  color: #fff;
  font-weight: bold;
  font-size: 18px;
  font-family: Arial, sans-serif;
}

/* 모바일 최적화 */
@media (max-width: 480px) {
  .modal-content {
    width: 95%;
    margin: 10px;
  }
  
  .logo {
    max-width: 200px;
    height: auto;
  }
  
  .app-title {
    font-size: 24px;
  }
  
  .social-login-section {
    gap: 12px;
    max-width: 180px;
  }
  
  .social-btn {
    width: 45px;
    height: 45px;
  }
}
